import { validateRegister } from "@/app/register/validateRegister"
import { User, LoginDetail } from "@/types/User"
import { LOGIN_URL, REGISTER_URL, USER_URL } from "@/utils/API_URL"

export const userServices = {
    register: async (newUser: User) => {
        //validate newUser
        const newUserValid = validateRegister(newUser)
        if (!newUserValid) {
            alert("user is not valid to register")
            return
        }
        //try create new user

        const config = {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(newUser),
        }

        try {
            const req = await fetch(REGISTER_URL, config)
            console.log(req)

            if (req.ok) {
                const res = await req.json()
                return res
            } else {
                const res = await req.json()
                return res
            }
        } catch (error) {
            console.log(error)
            return false
        }
    },
    login: async (loginDetail: LoginDetail) => {
        try {
            const res = await fetch(LOGIN_URL, {
                method: "POST",
                headers: { "content-type": "application/json" },
                body: JSON.stringify(loginDetail),
                credentials: "include",
            })
            if (res.ok) {
                res.json()
                console.log(res)
                return true
            } else {
                console.log("res not ok")
                return false
            }
        } catch (error) {
            console.log(error)
            return false
        }
    },
    getUserData: async (id: string) => {
        try {
            const req = await fetch(`${USER_URL}/${id}`, {
                credentials: "include",
                method: "POST",
                headers: { "content-type": "application/json" },
            })
            if (req.ok) {
                const res = await req.json()
                console.log(res)
                return { success: true, data: res.data }
            } else {
                console.log("res not ok")
                return { success: false }
            }
        } catch (error) {
            console.log(error)
            return { success: false, data: undefined, error }
        }
    },
}
